.__contentOrderConfirmation{
    padding: 30px;
    width: 28rem;
    height: 28rem;
    border: 1px solid #FFFF;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    color: #FFFF;
 }

._contentTitleOrder{
 display: flex;
 flex-direction: column;
}

._contentDescriptionOrder{

}
.__titleOrderConfirmation h1{ 
    font-family: 'Inter', sans-serif;
    font-weight: 600;
    font-size: 1.2rem;
}

.__titleOrderConfirmation{
    display: flex;
    justify-content: center;
}

.__subTitleOrderConfirmation{
    display: flex; 
    width:100%;
    justify-content: center
}

._subTitleOrderConfirmation{
   width: 20rem
}

.__titleWaitConfirmation{
 width: 100%;
 padding-bottom: .8rem;
}

.__titleWaitConfirmation h1{
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: .8rem;
}

.__subTitleWaitConfirmation h1{
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: .8rem;
}

.__subTitleOrderConfirmation h1{
    font-family: 'Inter', sans-serif;
    font-weight: 600;
    font-size: 1rem;
}

.__needHelp{
    padding-top: 1rem;
    font-family: 'Inter', sans-serif; 
    font-weight: 400;
    font-size: .4rem;
}

.p{
 text-decoration: underline;
}

.__subTitleWaitConfirmation p{
 text-decoration: underline;
}


